@import url('https://fonts.googleapis.com/css?family=Lato:400,700&display=swap');

:root {
  --color-white: #fff;
  --color-black: #333;
  --color-cloud: #ecf0f1;
  --color-silver: #bdc3c7;
  --color-yellow: #f1c40f;
  --color-bg: #c7c1bd;
  --box-shadow: 0 0 1.5rem var(--color-yellow);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Lato', sans-serif; 
  font-weight: 400;
  background-color: var(--color-bg);
}

main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#drum-machine {
  width: 50%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
  padding: 1rem;
  background-color: var(--color-silver);
  border: 3px solid var(--color-yellow);
  box-shadow: var(--box-shadow);
  

  #pad-container {
    font-size: 4rem;
    display: grid;
    grid-template-columns: repeat(3, 10rem);
    grid-template-rows: repeat(3, 10rem);
    gap: 1rem;
  }

  .drum-pad {
    width: 100%;
    height: 100%;
    color: var(--color-white);
    background-color: var(--color-black);
    border-radius: .4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .03s;
  }

  #display {
    font-size: 4rem;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-cloud);
  }
}

.playing {
  transform: scale(.9);
  border-color: var(--color-yellow);
  box-shadow: var(--box-shadow);
}

// 900px
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }

  #drum-machine {
    width: 60%;
  }
}

// 600px
@media only screen and (max-width: 37.5em) {
  #drum-machine {
    width: 90%;
  }
}

// 500px
@media only screen and (max-width: 31.25em) {
  #drum-machine {
    grid-template-columns: 1fr;
    row-gap: 1rem;

    #pad-container {
      justify-self: center; 
    }
  }
}

// 300px 
@media only screen and (max-width: 18.75em) {
  html {
    font-size: 40%;
  }
}