@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
:root{--color-white: #fff;--color-black: #333;--color-cloud: #ecf0f1;--color-silver: #bdc3c7;--color-yellow: #f1c40f;--color-bg: #c7c1bd;--box-shadow: 0 0 1.5rem var(--color-yellow)}*{padding:0;margin:0;box-sizing:inherit}html{font-size:62.5%}body{font-family:'Lato', sans-serif;font-weight:400;background-color:#c7c1bd;background-color:var(--color-bg)}main{min-height:100vh;display:flex;justify-content:center;align-items:center}#drum-machine{width:50%;display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:1rem;-webkit-column-gap:1rem;-moz-column-gap:1rem;column-gap:1rem;padding:1rem;background-color:#bdc3c7;background-color:var(--color-silver);border:3px solid #f1c40f;border:3px solid var(--color-yellow);box-shadow:0 0 1.5rem #f1c40f;box-shadow:var(--box-shadow)}#drum-machine #pad-container{font-size:4rem;display:grid;grid-template-columns:repeat(3, 10rem);grid-template-rows:repeat(3, 10rem);grid-gap:1rem;gap:1rem}#drum-machine .drum-pad{width:100%;height:100%;color:#fff;color:var(--color-white);background-color:#333;background-color:var(--color-black);border-radius:.4rem;display:flex;justify-content:center;align-items:center;-webkit-transition:all .03s;transition:all .03s}#drum-machine #display{font-size:4rem;font-weight:700;display:flex;justify-content:center;align-items:center;background-color:#ecf0f1;background-color:var(--color-cloud)}.playing{-webkit-transform:scale(0.9);transform:scale(0.9);border-color:#f1c40f;border-color:var(--color-yellow);box-shadow:0 0 1.5rem #f1c40f;box-shadow:var(--box-shadow)}@media only screen and (max-width: 56.25em){html{font-size:50%}#drum-machine{width:60%}}@media only screen and (max-width: 37.5em){#drum-machine{width:90%}}@media only screen and (max-width: 31.25em){#drum-machine{grid-template-columns:1fr;grid-row-gap:1rem;row-gap:1rem}#drum-machine #pad-container{justify-self:center}}@media only screen and (max-width: 18.75em){html{font-size:40%}}

